import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import logo from '../../../../inc/admin/images/zak-logo.png';
import { ROUTES } from '../constants';

const Header: React.FC = () => {
	const location = useLocation();
	return (
		<>
			<div className="py-3 px-5 bt-1 border-b border-[#E9E9E9] border-solid mb-8 bg-[#FFFFFF] ">
				<div className="container mx-auto lg:max-w-screen-xl px-5 box-border">
					<div className="header flex items-center gap-8">
						<div className="header__logo flex items-center">
							<Link to="/dashboard">
								<img src={logo} alt="Zakra" className="w-8 h-8" />
							</Link>
						</div>
						<div className="header__menu">
							<ul className="flex items-center gap-4">
								{ROUTES.map((route) => (
									<li
										className={`m-0 text-sm font-semibold ${location.pathname === route.route ? 'active' : ''}`}
									>
										<Link
											className="no-underline px-2 text-[#383838] focus:outline-0 hover:text-primary-500 focus:shadow-none"
											to={route.route}
										>
											{route.label}
										</Link>
									</li>
								))}
							</ul>
						</div>
					</div>
				</div>
			</div>
		</>
	);
};

export { Header };
